* {
  margin: 0;
  padding: 0;
}
.Loader {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  background: rgb(255 255 255 / 50%);
  z-index: 1;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
}
/* Remove Input Errors */
input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
